import goodsItem from '@/components/goods-item.vue';
export default {
	components: {
		goodsItem
	},
	data() {
		return {
			selectMenu: 1,
			classfiyVis:false,
			classfiy: [],
			selectClassfiy:'',
			sort:'zh',
			subSort:1,
			goodsItems: [],
			pageNumber: 1, //分页相关
			pageSize: 6, //
			noMore:false,
		};
	},
	methods: {
		navTo(url) {
			uni.navigateTo({
				url
			})
		},
		handleMenuChange(params) {
			this.selectMenu = params
		},
		onLoad() {
			this.getData()
			this.getClassfiy()
		},
		targetType(){
			  this.classfiyVis=!this.classfiyVis;
		},
		changeClassfiy(id){
		  this.selectClassfiy=id;
		  this.classfiyVis=false;
		  this.initLoad()
		},
		changeSort(sort){
			if(this.sort=='price'){
				if(sort=='price'){
					if(this.subSort==1){
						this.subSort=0
					}else{
						this.subSort=1
					}
				}else{
					this.sort=sort
					this.subSort=1
				}	
			}else{
					this.sort=sort
					this.subSort=1
			}
			this.initLoad()
		},
		
		initLoad(){
		   this.pageNumber=1
		   this.noMore=false
		   this.goodsItems=[]
		   this.getData()
		},
		getData() {
			if(this.noMore){
				return
			}
			this.$getJson('/api/v2/vue/sqPlus/resources/resourcesList.jsp', {
				companyId: 202,
				pageNumber: this.pageNumber,
				catId:this.selectClassfiy,
				pageSize: this.pageSize,
				sort:this.sort+','+this.subSort
			}, 'POST', res => {
				const result=res.data||[]
				if(this.pageNumber==1){
					this.goodsItems = result
				}else{
					this.goodsItems = [...this.goodsItems,...result]
				}
				if(result.length<this.pageSize){
					this.noMore=true
				}
			
			    this.pageNumber=this.pageNumber+1
			})
		   
		},
		//加载更多
		onReachBottom(){
			this.getData();
		},
		getClassfiy(){
			this.$getJson('/api/v3/store/goods/category/list.jsp', {
				storeId: 202
			}, 'POST', res => {
				this.classfiy = res.data.industry
				console.log(this.classfiy)
			})
		}

	}
}
